<template>
<div >
   <mu-appbar class="fixed" :title="videoObject.title">
     <mu-icon-button icon="arrow_back" slot="left" @click="goback()" />
   </mu-appbar>
<div style="padding-top:66px;">
 <mu-card>
   <mu-card-media >
    <div id="player" class="flowplayer" >
     
    </div>
  
  </mu-card-media>
 </mu-card>
 <mu-tabs :value="currentTab" @change="handleTabChange">
    <mu-tab value="tab1" title="信息"/>
    <mu-tab value="tab2" title="图集"/>
    <mu-tab value="tab3" title="视频"/>
  </mu-tabs>
  <div class="desc-container" v-if="currentTab === 'tab1'">
   <mu-card-title :title="videoObject.title"  subTitle=""/>
      <mu-card-text>
   
  </mu-card-text>
  </div>
  <div v-if="currentTab === 'tab2'"> 
  
  </div>
  <div v-if="currentTab === 'tab3'">
    <h2></h2>
    <p>
      
    </p>
  </div>
</div>
<mu-dialog v-if="!logined" :open="dialog" title="广告">
    <img :src="videoObject.img" width="300px" height="auto">
    <mu-flat-button  label="关闭" slot="actions" primary @click="()=>{dialog=false}"/>
  </mu-dialog>
</div>
</template>
<script >
	export default {
		name:'vipvideo',

    mounted(){

        this.player=document.getElementById("player")
        this.initplayer()

    },
		
		data(){
			return {
				currentTab:'tab1',
        dialog:true,
        videoplayerurl:'',
       
        player:null
			}
		},
   
    beforeRouteEnter(to,from,next)
    {
      next(vm=>{
        vm.initplayer()
      })
    },

    beforeRouteLeave(to,from,next)
    {
       this.player.innerHTML=""
       if(this.flowplayer) {
        this.flowplayer.shutdown()
      }
       next(true)
     

    },
		computed:{
       videoObject() {
        return this.$store.state.vipvo
       },
       logined(){
      return this.$store.state.loginedUser
      }
    },
		methods: {
      initplayer(){
          var vurl=this.videoObject.url
          if(this.videoObject.type=='2'){
            vurl=this.videoObject.url.replace('jpp','v')
          }

         
          this.flowplayer=flowplayer(this.player,{
            poster:this.videoObject.img,
            logo: "https://flowplayer.org/media/img/mylogo.png",
            clip:{
              qualities:["360p","720p"],
              sources:[{
                 type:"video/mp4",
                src:vurl
              }]
            }
          })
          this.flowplayer.on("error",function(e,api){
            e.preventDefault()
          })
          

         this.dialog=!this.logined
      },
      resetplayer()
      {   
          flowplayer.conf.poster=this.videoObject.img
          this.flowplayer.load({
              sources:[{
                type:"video/mp4",
                src:this.videoObject.url
              }]
            })
          this.dialog=true
      
      },

			fetchData () {
				alert('hello')
			},
      handleTabChange (val) {
        this.currentTab = val
      },
      goback()
      {
        this.$router.go(-1)
      }
     

    } 
  }
</script>
<style scoped>
  
   .flex{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
   }
   
   .desc-container{
    height:100%;
    overflow-y: scroll;
   }
   img {
    width: 100%;
   }
   .fixed {
    position:fixed;
    top: 0;
   }
 
</style>


